import {useQuery, UseQueryOptions, UseQueryResult} from '@tanstack/react-query';
import baseUrl from '../baseUrl';
import {useCreateAuthenticatedFetch} from '../useCreateAuthenticatedFetch';
import {handleResponse} from '../handleResponse';
import ProblemDetailsError from '../ProblemDetailsError';
import { ProjectsDashboardData } from '../../types';

// Request parameters for the API
export interface ProjectsDashboardRequest {
    startGlobalWeekNumber?: number;
    numberOfWeeks?: number;
}

type ProjectsDashboardDataQueryOptions = Omit<
    UseQueryOptions<
        ProjectsDashboardData,
        Error | ProblemDetailsError,
        ProjectsDashboardData,
        readonly unknown[]
    >,
    'queryKey' | 'queryFn'
>;

const useGetProjectsDashboardData = (
    params: ProjectsDashboardRequest,
    options?: ProjectsDashboardDataQueryOptions
): UseQueryResult<ProjectsDashboardData, Error> => {
    const authenticatedFetch = useCreateAuthenticatedFetch();

    // Build query string from parameters
    const queryString = params
        ? new URLSearchParams(
              // Filter out any null/undefined values before creating the search params
              Object.fromEntries(
                  Object.entries(params).filter(([, v]) => v != null)
              )
          ).toString()
        : '';

    const url = `${baseUrl}/api/projects/dashboard-data${queryString ? `?${queryString}` : ''}`;

    // Include params in query key for proper caching
    const queryKey = ['projects-dashboard-data', params] as const;

    return useQuery({
        queryKey,
        queryFn: async () => {
            const response = await authenticatedFetch(url);
            return handleResponse<ProjectsDashboardData>(response);
        },
        ...options
    });
};

export default useGetProjectsDashboardData;
