/**
 * Color constants for resource allocation heatmap
 */
const COLORS = {
    EMPTY: '#f8fafc',
    GREEN: 'rgba(34, 197, 94, 0.7)',
    AMBER: 'rgba(245, 158, 11, 0.7)',
    RED: 'rgba(239, 68, 68, 0.7)'
} as const;

/**
 * Thresholds for allocation levels
 */
const THRESHOLDS = {
    BAU_OVERALLOCATION: 1.0,
    NON_BAU_HIGH: 0.5,
    NON_BAU_MEDIUM: 0.25
} as const;

/**
 * Priority levels for color selection (higher number = higher priority)
 */
enum ColorPriority {
    GREEN = 1,
    AMBER = 2,
    RED = 3
}

/**
 * Determines the severity level and color for a given allocation type
 */
function getAllocationSeverity(
    percentage: number,
    type: 'BAU' | 'NON_BAU'
): {color: string; priority: ColorPriority} {
    if (type === 'BAU') {
        return percentage > THRESHOLDS.BAU_OVERALLOCATION
            ? {color: COLORS.RED, priority: ColorPriority.RED}
            : {color: COLORS.GREEN, priority: ColorPriority.GREEN};
    }

    // NON_BAU logic
    if (percentage > THRESHOLDS.NON_BAU_HIGH) {
        return {color: COLORS.RED, priority: ColorPriority.RED};
    }
    if (percentage > THRESHOLDS.NON_BAU_MEDIUM) {
        return {color: COLORS.AMBER, priority: ColorPriority.AMBER};
    }
    return {color: COLORS.GREEN, priority: ColorPriority.GREEN};
}

/**
 * Returns a heatmap color based on allocation percentages.
 * Used to indicate resource allocation levels with traffic light colors.
 *
 * @param percentageBAU - BAU allocation percentage (0.0 to 1.0+)
 * @param percentageNonBAU - NonBAU allocation percentage (0.0 to 1.0+)
 * @returns CSS color string
 */
export const getHeatmapColor = (
    percentageBAU: number,
    percentageNonBAU: number
): string => {
    // Handle empty/zero allocations
    const hasBAU = percentageBAU > 0;
    const hasNonBAU = percentageNonBAU > 0;

    if (!hasBAU && !hasNonBAU) {
        return COLORS.EMPTY;
    }

    // Get severity for each allocation type
    const severities = [];

    if (hasBAU) {
        severities.push(getAllocationSeverity(percentageBAU, 'BAU'));
    }

    if (hasNonBAU) {
        severities.push(getAllocationSeverity(percentageNonBAU, 'NON_BAU'));
    }

    // Return the color with the highest priority (most restrictive)
    const highestPriority = Math.max(...severities.map(s => s.priority));
    const selectedSeverity = severities.find(
        s => s.priority === highestPriority
    );

    return selectedSeverity?.color ?? COLORS.GREEN;
};

/**
 * Returns a warning color for specific warning types
 * @param type - The type of warning ('red', 'amber', 'green')
 * @returns CSS color string
 */
export const getWarningColor = (type: 'red' | 'amber' | 'green'): string => {
    switch (type) {
        case 'red':
            return COLORS.RED;
        case 'amber':
            return COLORS.AMBER;
        case 'green':
            return COLORS.GREEN;
        default:
            return COLORS.GREEN;
    }
};
