import React from 'react';
import {Calendar, Clock, Users} from 'lucide-react';

interface AllocationInfo {
    id: string;
    globalWeekNumber: number;
    weekBeginningOn: string;
    weekEndOn: string;
    hoursBAU: number;
    hoursNonBAU: number;
    percentageBAU?: number;
    percentageNonBAU?: number;
    description?: string;
    phaseName?: string;
    phaseColor?: string;
}

interface AllocationTooltipProps {
    allocation: AllocationInfo;
    resourceName: string;
    projectType: string;
    totalWeeklyHours?: number;
    totalWeeklyPercentage?: number;
    availableHours?: number;
    baseHours?: number;
    teamAllocations?: Array<{
        teamId: string;
        teamName: string;
        hoursBAU: number;
        hoursNonBAU: number;
    }>;
}

const AllocationTooltip: React.FC<AllocationTooltipProps> = ({
    allocation,
    resourceName,
    projectType,
    totalWeeklyHours,
    totalWeeklyPercentage,
    availableHours,
    baseHours,
    teamAllocations
}) => {
    const weekStart = new Date(allocation.weekBeginningOn).toLocaleDateString('en-US', { 
        month: 'short', 
        day: 'numeric' 
    });
    const weekEnd = new Date(allocation.weekEndOn).toLocaleDateString('en-US', { 
        month: 'short', 
        day: 'numeric' 
    });

    return (
        <div style={{
            background: 'rgba(0, 0, 0, 0.9)',
            color: 'white',
            padding: '16px',
            borderRadius: '8px',
            fontSize: '13px',
            lineHeight: '1.5',
            minWidth: '320px',
            maxWidth: '400px',
            boxShadow: '0 4px 12px rgba(0, 0, 0, 0.3)',
            zIndex: 1000,
            position: 'absolute',
            pointerEvents: 'none'
        }}>
            <div style={{ fontWeight: '600', marginBottom: '8px', color: '#60a5fa' }}>
                {resourceName}
            </div>
            
            <div style={{ marginBottom: '6px' }}>
                <strong>Project:</strong> {projectType}
            </div>
            
            <div style={{ marginBottom: '6px', display: 'flex', alignItems: 'center', gap: '4px' }}>
                <Calendar size={12} />
                <span>Week {allocation.globalWeekNumber} ({weekStart} - {weekEnd})</span>
            </div>
            
            <div style={{ marginBottom: '6px', display: 'flex', alignItems: 'center', gap: '4px' }}>
                <Clock size={12} />
                <span>This project: {(allocation.hoursBAU + allocation.hoursNonBAU).toFixed(1)}h</span>
                {(allocation.hoursBAU > 0 && allocation.hoursNonBAU > 0) && (
                    <span style={{ fontSize: '11px', color: '#94a3b8' }}>
                        ({allocation.hoursBAU}h BAU + {allocation.hoursNonBAU}h NonBAU)
                    </span>
                )}
            </div>
            
            {allocation.phaseName && (
                <div style={{
                    marginBottom: '6px',
                    padding: '6px 8px',
                    background: allocation.phaseColor || 'rgba(255, 255, 255, 0.1)',
                    borderRadius: '4px',
                    fontSize: '11px',
                    fontWeight: '500',
                    color: 'white'
                }}>
                    Phase: {allocation.phaseName}
                </div>
            )}
            
            {allocation.description && (
                <div style={{
                    marginBottom: '6px',
                    padding: '8px',
                    background: 'rgba(255, 255, 255, 0.1)',
                    borderRadius: '4px',
                    fontSize: '12px',
                    fontStyle: 'italic'
                }}>
                    <strong>Task:</strong> {allocation.description}
                </div>
            )}
            
            {teamAllocations && teamAllocations.length > 0 && (
                <div style={{
                    marginTop: '8px',
                    paddingTop: '8px',
                    borderTop: '1px solid rgba(255, 255, 255, 0.2)',
                    fontSize: '11px'
                }}>
                    <div style={{ display: 'flex', alignItems: 'center', gap: '4px', marginBottom: '4px', fontWeight: '600' }}>
                        <Users size={12} />
                        <span>Team Allocations:</span>
                    </div>
                    {teamAllocations.map(team => (
                        <div key={team.teamId} style={{ marginLeft: '16px', marginBottom: '2px' }}>
                            {team.teamName}: {(team.hoursBAU + team.hoursNonBAU).toFixed(1)}h
                            {(team.hoursBAU > 0 && team.hoursNonBAU > 0) && (
                                <span style={{ fontSize: '10px', color: '#94a3b8' }}>
                                    {' '}({team.hoursBAU}h BAU + {team.hoursNonBAU}h NonBAU)
                                </span>
                            )}
                        </div>
                    ))}
                </div>
            )}

            {(totalWeeklyHours && totalWeeklyPercentage) || availableHours || baseHours ? (
                <div style={{
                    marginTop: '8px',
                    paddingTop: '8px',
                    borderTop: '1px solid rgba(255, 255, 255, 0.2)',
                    fontSize: '11px'
                }}>
                    {totalWeeklyHours && totalWeeklyPercentage && (
                        <>
                            <div>Total week allocation: {totalWeeklyHours.toFixed(1)}h</div>
                            <div>Capacity utilization: {Math.round(totalWeeklyPercentage * 100)}%</div>
                        </>
                    )}
                    {availableHours !== undefined && (
                        <div>Available hours this week: {availableHours.toFixed(1)}h</div>
                    )}
                    {baseHours !== undefined && availableHours !== undefined && baseHours !== availableHours && (
                        <div style={{ fontSize: '10px', color: '#94a3b8' }}>
                            (Base: {baseHours.toFixed(1)}h, reduced by holidays/leave)
                        </div>
                    )}
                </div>
            ) : null}
        </div>
    );
};

export default AllocationTooltip;