import React from 'react';
import {format} from 'date-fns';
import {Filter} from 'lucide-react';
import {TimelineData} from './DashboardContainer';
import { getWeekInfo } from '../../../lib/scheduling';

interface TimelineHeaderProps {
    timelineData: TimelineData;
    title: string;
    onToggleFilter?: () => void;
    isFilterVisible?: boolean;
    hasActiveFilters?: boolean;
}

const TimelineHeader: React.FC<TimelineHeaderProps> = ({
    timelineData,
    title,
    onToggleFilter,
    isFilterVisible,
    hasActiveFilters
}) => {
    return (
        <div
            style={{
                position: 'sticky',
                top: 0,
                background: 'white',
                zIndex: 10,
                borderBottom: '2px solid #e2e8f0',
                marginBottom: '8px'
            }}
        >
            {/* Year Groups */}
            {timelineData.yearGroups.length > 0 && (
                <div
                    style={{
                        display: 'flex',
                        borderBottom: '1px solid #e5e7eb'
                    }}
                >
                    <div
                        style={{
                            width: '300px',
                            padding: '4px 16px',
                            fontWeight: '500',
                            color: '#6b7280',
                            fontSize: '11px',
                            borderRight: '1px solid #e5e7eb'
                        }}
                    >
                        Year
                    </div>
                    <div style={{flex: 1, display: 'flex'}}>
                        {timelineData.yearGroups.map((group, idx) => (
                            <div
                                key={`${group.label}-year-${idx}`}
                                style={{
                                    width: `${(group.span / timelineData.columns.length) * 100}%`,
                                    textAlign: 'center',
                                    padding: '4px',
                                    fontSize: '11px',
                                    fontWeight: '500',
                                    color: '#6b7280',
                                    borderRight: '1px solid #eee'
                                }}
                            >
                                {group.label}
                            </div>
                        ))}
                    </div>
                </div>
            )}

            {/* Quarter Groups */}
            {timelineData.quarterGroups.length > 0 && (
                <div
                    style={{
                        display: 'flex',
                        borderBottom: '1px solid #e5e7eb'
                    }}
                >
                    <div
                        style={{
                            width: '300px',
                            padding: '4px 16px',
                            fontWeight: '500',
                            color: '#6b7280',
                            fontSize: '11px',
                            borderRight: '1px solid #e5e7eb'
                        }}
                    >
                        Quarter
                    </div>
                    <div style={{flex: 1, display: 'flex'}}>
                        {timelineData.quarterGroups.map((group, idx) => (
                            <div
                                key={`${group.label}-quarter-${idx}`}
                                style={{
                                    width: `${(group.span / timelineData.columns.length) * 100}%`,
                                    textAlign: 'center',
                                    padding: '4px',
                                    fontSize: '11px',
                                    fontWeight: '500',
                                    color: '#6b7280',
                                    borderRight: '1px solid #eee'
                                }}
                            >
                                {group.label}
                            </div>
                        ))}
                    </div>
                </div>
            )}

            {/* Month Groups */}
            {timelineData.monthGroups.length > 0 && (
                <div
                    style={{
                        display: 'flex',
                        borderBottom: '1px solid #e5e7eb'
                    }}
                >
                    <div
                        style={{
                            width: '300px',
                            padding: '4px 16px',
                            fontWeight: '500',
                            color: '#6b7280',
                            fontSize: '11px',
                            borderRight: '1px solid #e5e7eb'
                        }}
                    >
                        Month
                    </div>
                    <div style={{flex: 1, display: 'flex'}}>
                        {timelineData.monthGroups.map((group, idx) => (
                            <div
                                key={`${group.label}-month-${idx}`}
                                style={{
                                    width: `${(group.span / timelineData.columns.length) * 100}%`,
                                    textAlign: 'center',
                                    padding: '4px',
                                    fontSize: '11px',
                                    fontWeight: '500',
                                    color: '#6b7280',
                                    borderRight: '1px solid #eee'
                                }}
                            >
                                {group.label}
                            </div>
                        ))}
                    </div>
                </div>
            )}

            {/* Column Headers */}
            <div style={{display: 'flex', minHeight: '40px'}}>
                <div
                    style={{
                        width: '300px',
                        padding: '8px 16px',
                        fontWeight: '600',
                        color: '#374151',
                        borderRight: '1px solid #e5e7eb',
                        display: 'flex',
                        alignItems: 'center',
                        justifyContent: 'space-between'
                    }}
                >
                    <span>{title}</span>
                    {onToggleFilter && (
                        <button
                            onClick={onToggleFilter}
                            style={{
                                background: 'none',
                                border: 'none',
                                cursor: 'pointer',
                                padding: '4px',
                                borderRadius: '4px',
                                display: 'flex',
                                alignItems: 'center',
                                color: hasActiveFilters ? '#3b82f6' : '#6b7280',
                                backgroundColor: isFilterVisible ? '#f3f4f6' : 'transparent'
                            }}
                            title={isFilterVisible ? 'Hide filters' : 'Show filters'}
                        >
                            <Filter size={16} />
                            {hasActiveFilters && (
                                <div
                                    style={{
                                        width: '6px',
                                        height: '6px',
                                        borderRadius: '50%',
                                        backgroundColor: '#3b82f6',
                                        position: 'absolute',
                                        marginLeft: '10px',
                                        marginTop: '-8px'
                                    }}
                                />
                            )}
                        </button>
                    )}
                </div>
                <div
                    style={{
                        flex: 1,
                        display: 'flex',
                        background: '#f9fafb'
                    }}
                >
                    {timelineData.columns.map((col, index) => {
                        // Check if this column represents today's week
                        const today = new Date();
                        const todayWeekInfo = getWeekInfo(today);
                        const isToday = col.globalWeekNumber === todayWeekInfo.globalWeekNumber;
                        
                        // Calculate position within the week (0 = Monday, 6 = Sunday)
                        let dayPositionPercent = 50; // Default to center
                        if (isToday) {
                            const mondayOfThisWeek = todayWeekInfo.mondayOfWeek;
                            const dayOfWeek = Math.floor((today.getTime() - mondayOfThisWeek.getTime()) / (1000 * 60 * 60 * 24));
                            // Convert day of week (0-6) to percentage position within the week column
                            // Add small margins (10% on each side) so arrow doesn't touch edges
                            dayPositionPercent = 10 + (Math.max(0, Math.min(6, dayOfWeek)) / 6) * 80;
                        }
                        
                        return (
                            <div
                                key={`${col.label}-${index}-col`}
                                style={{
                                    flex: 1,
                                    borderRight: '1px solid #e5e7eb',
                                    padding: '8px 4px',
                                    fontSize: '12px',
                                    color: '#6b7280',
                                    textAlign: 'center',
                                    minWidth: '50px',
                                    position: 'relative'
                                }}
                            >
                                <div>{col.label}</div>
                                <div style={{fontSize: '10px', opacity: 0.7}}>
                                    {format(col.startDate, 'MMM d')}
                                </div>
                                
                                {/* Today indicator arrow */}
                                {isToday && (
                                    <div
                                        style={{
                                            position: 'absolute',
                                            bottom: '-12px',
                                            left: `${dayPositionPercent}%`,
                                            transform: 'translateX(-50%)',
                                            zIndex: 1000,
                                            pointerEvents: 'none'
                                        }}
                                        title={`Today: ${today.toLocaleDateString()}`}
                                    >
                                        <div
                                            style={{
                                                width: 0,
                                                height: 0,
                                                borderLeft: '8px solid transparent',
                                                borderRight: '8px solid transparent',
                                                borderTop: '12px solid #dc2626',
                                                filter: 'drop-shadow(0 2px 4px rgba(220, 38, 38, 0.3))'
                                            }}
                                        />
                                    </div>
                                )}
                            </div>
                        );
                    })}
                </div>
            </div>
        </div>
    );
};

export default TimelineHeader;
