import React, { useState, useEffect, useRef, useMemo } from 'react';
import { Edit2, Save, X } from 'lucide-react';

import { getHeatmapColor, getWarningColor } from '../../../lib/heatmapColors';
import AllocationTooltip from './AllocationTooltip';
import { useTooltip } from '../../../hooks/useTooltip';
import { ProjectResource, DashboardProjectModel, DashboardAllocationModel, EngagementContext, AssignmentType } from '../../../types';

// This is a simplified version of the calculation logic for the cell's context
interface WeeklyTotals {
    totalHours: number;
    totalHoursBAU: number;
    totalHoursNonBAU: number;
    availableHours?: number; // Week-specific available hours for this resource
}

interface ProjectResourceAllocationCellProps {
    allocation: DashboardAllocationModel | null; // Can be null for empty cells
    resource: ProjectResource;
    project: DashboardProjectModel;
    globalWeekNumber: number;
    canEdit: boolean;
    onAllocationChange: (allocationId: string | null, projectId: string, resourceId: string, globalWeekNumber: number, newHours: { hoursBAU: number; hoursNonBAU: number }, assignmentType: AssignmentType) => void;
    getBarStyles: (startWeek: number, duration: number) => React.CSSProperties | null;
    calculateTotalWeeklyAllocation: (resourceId: string, globalWeekNumber: number) => WeeklyTotals;
}

const ProjectResourceAllocationCell: React.FC<ProjectResourceAllocationCellProps> = ({
    allocation,
    resource,
    project,
    globalWeekNumber,
    canEdit,
    onAllocationChange,
    getBarStyles,
    calculateTotalWeeklyAllocation,
}) => {
    const [isEditing, setIsEditing] = useState(false);
    const [isHovered, setIsHovered] = useState(false);
    const [editedHours, setEditedHours] = useState({
        hoursBAU: allocation?.hoursBAU ?? 0,
        hoursNonBAU: allocation?.hoursNonBAU ?? 0,
    });
    const popoverRef = useRef<HTMLDivElement>(null);
    const { tooltipProps, TooltipPortal } = useTooltip();

    // Reset local state if the source allocation changes (e.g., after saving)
    useEffect(() => {
        setEditedHours({
            hoursBAU: allocation?.hoursBAU ?? 0,
            hoursNonBAU: allocation?.hoursNonBAU ?? 0,
        });
    }, [allocation]);

    // Close popover if clicking outside
    useEffect(() => {
        const handleClickOutside = (event: MouseEvent) => {
            if (popoverRef.current && !popoverRef.current.contains(event.target as Node)) {
                handleCancel();
            }
        };
        if (isEditing) {
            document.addEventListener('mousedown', handleClickOutside);
        }
        return () => {
            document.removeEventListener('mousedown', handleClickOutside);
        };
    }, [isEditing]);

    const handleSave = () => {
        // For existing allocations, pass the allocation ID. For new allocations, pass null
        const allocationId = allocation ? allocation.id : null;
        
        // For BAU projects, ensure Non-BAU hours are always 0
        const hoursToSave = project.engagementContext === EngagementContext.BAU
            ? { ...editedHours, hoursNonBAU: 0 }
            : editedHours;
            
        onAllocationChange(allocationId, project.id, resource.id, globalWeekNumber, hoursToSave, allocation?.assignmentType || resource.assignmentType);
        setIsEditing(false);
    };
    
    const handleCancel = () => {
        // Reset to original values
        setEditedHours({
            hoursBAU: allocation?.hoursBAU ?? 0,
            hoursNonBAU: allocation?.hoursNonBAU ?? 0,
        });
        setIsEditing(false);
    };

    const weeklyTotals = calculateTotalWeeklyAllocation(resource.id, globalWeekNumber);
    
    // Use week-specific available hours if available, otherwise fall back to 40
    const availableHours = weeklyTotals.availableHours ?? 40;
    
    // Determine the heatmap color based on the resource's total load for the week
    const backgroundColor = getHeatmapColor(
        weeklyTotals.totalHoursBAU / availableHours,
        weeklyTotals.totalHoursNonBAU / availableHours
    );
    
    // This is for the specific hours shown in this cell, not the total
    const totalHoursInCell = (allocation?.hoursBAU ?? 0) + (allocation?.hoursNonBAU ?? 0);
    const hasAllocation = allocation && totalHoursInCell > 0;

    const barStyle = getBarStyles(globalWeekNumber, 1);

    const tooltipContent = useMemo(() => (
        allocation && <AllocationTooltip
            allocation={allocation}
            resourceName={resource.name}
            projectType={project.name}
            totalWeeklyHours={weeklyTotals.totalHours}
            totalWeeklyPercentage={weeklyTotals.totalHours / availableHours}
        />
    ), [allocation, resource.name, project.name, weeklyTotals]);

    const bauOverAllocationWarning = weeklyTotals.totalHoursBAU > availableHours;

    // Early return after all hooks have been called
    if (!barStyle) return null;

    const renderDisplayMode = () => (
        <>
            <TooltipPortal />
            <div
                style={{
                    ...barStyle,
                    background: hasAllocation ? backgroundColor : 'transparent',
                    border: bauOverAllocationWarning ? `1px solid ${getWarningColor('red')}` : (hasAllocation ? 'none' : '1px dashed #e5e7eb'),
                    cursor: canEdit ? 'pointer' : 'default',
                    display: 'flex',
                    alignItems: 'center',
                    justifyContent: 'center',
                    color: 'white',
                    fontWeight: 500,
                    fontSize: '11px',
                }}
                onClick={() => canEdit && setIsEditing(true)}
                onMouseEnter={e => {
                    setIsHovered(true);
                    if (hasAllocation && !isEditing) tooltipProps.onMouseEnter(e, tooltipContent);
                }}
                onMouseMove={e => {
                    if (hasAllocation && !isEditing) tooltipProps.onMouseMove(e);
                }}
                onMouseLeave={() => {
                    setIsHovered(false);
                    if (hasAllocation && !isEditing && tooltipProps.onMouseLeave) tooltipProps.onMouseLeave();
                }}
            >
                {hasAllocation ? `${totalHoursInCell.toFixed(1)}h` : ''}
                {canEdit && (
                    <Edit2 size={12} style={{
                        marginLeft: 4,
                        opacity: isHovered ? 1 : 0,
                        transition: 'opacity 0.2s'
                    }}/>
                )}
            </div>
        </>
    );
    
    const renderEditMode = () => {
        const isBAUProject = project.engagementContext === EngagementContext.BAU;
        
        return (
            // Wrapper to keep the original cell space occupied
            <div style={{ ...barStyle, background: 'transparent' }}>
                <div
                    ref={popoverRef}
                    style={{
                        position: 'absolute',
                        zIndex: 20,
                        background: 'white',
                        border: '1px solid #d1d5db',
                        borderRadius: '8px',
                        boxShadow: '0 4px 12px rgba(0,0,0,0.15)',
                        padding: '12px',
                        width: '200px',
                        transform: 'translateY(-100%)', // Position above the cell
                        marginTop: '-8px'
                    }}
                >
                    <div style={{ fontWeight: 600, fontSize: '13px', marginBottom: '8px' }}>
                        {allocation ? 'Edit Allocation' : 'Add Allocation'}
                    </div>
                    
                    {/* For NonBAU projects, show Non-BAU Hours first */}
                    {!isBAUProject && (
                        <div style={{ marginBottom: '8px' }}>
                            <label style={{ display: 'block', fontSize: '11px', color: '#4b5563', marginBottom: '4px' }}>Non-BAU Hours</label>
                            <input
                                type="number"
                                step="1"
                                min="0"
                                value={editedHours.hoursNonBAU}
                                onChange={e => setEditedHours(prev => ({ ...prev, hoursNonBAU: parseFloat(e.target.value) || 0 }))}
                                style={{ width: '100%', padding: '6px', borderRadius: '4px', border: '1px solid #d1d5db' }}
                            />
                        </div>
                    )}
                    
                    {/* Always show BAU Hours */}
                    <div style={{ marginBottom: isBAUProject ? '0px' : '8px' }}>
                        <label style={{ display: 'block', fontSize: '11px', color: '#4b5563', marginBottom: '4px' }}>BAU Hours</label>
                        <input
                            type="number"
                            step="1"
                            min="0"
                            value={editedHours.hoursBAU}
                            onChange={e => setEditedHours(prev => ({ ...prev, hoursBAU: parseFloat(e.target.value) || 0 }))}
                            style={{ width: '100%', padding: '6px', borderRadius: '4px', border: '1px solid #d1d5db' }}
                        />
                    </div>
                    
                    <div style={{ display: 'flex', justifyContent: 'flex-end', gap: '8px', marginTop: '12px' }}>
                        <button onClick={handleCancel} style={{ padding: '4px 8px', border: '1px solid #d1d5db', borderRadius: '4px', background: 'white', cursor: 'pointer' }}><X size={14}/></button>
                        <button onClick={handleSave} style={{ padding: '4px 8px', border: 'none', borderRadius: '4px', background: '#4F46E5', color: 'white', cursor: 'pointer' }}><Save size={14}/></button>
                    </div>
                </div>
            </div>
        );
    };

    return isEditing ? renderEditMode() : renderDisplayMode();
};

export default ProjectResourceAllocationCell;