import { ResourceTimeline, ProjectTimeline, AssignmentType } from '../types';

/**
 * Result type for person allocation calculations
 */
export interface PersonAllocationResult {
    directHoursBAU: number;
    directHoursNonBAU: number;
    teamHoursBAU: number;
    teamHoursNonBAU: number;
    totalHoursBAU: number;
    totalHoursNonBAU: number;
    availableHours?: number;
    baseHours?: number;
    teamAllocations: Array<{
        teamId: string;
        teamName: string;
        hoursBAU: number;
        hoursNonBAU: number;
    }>;
}

/**
 * Calculate total allocation for a person in the Resources view, considering team memberships
 * @param personId - The person's ID
 * @param globalWeekNumber - The week to calculate for
 * @param allResources - All resources from ResourcesView
 * @returns PersonAllocationResult with breakdown of direct and team hours
 */
export function calculatePersonAllocationFromResources(
    personId: string,
    globalWeekNumber: number,
    allResources: ResourceTimeline[]
): PersonAllocationResult {
    // Find the person resource
    const personResource = allResources.find(r => r.id === personId && r.type === AssignmentType.Person);
    
    if (!personResource) {
        return {
            directHoursBAU: 0,
            directHoursNonBAU: 0,
            teamHoursBAU: 0,
            teamHoursNonBAU: 0,
            totalHoursBAU: 0,
            totalHoursNonBAU: 0,
            teamAllocations: []
        };
    }

    // Get direct person allocations for this week
    const personWeeklyAllocation = personResource.allocations.find(a => a.globalWeekNumber === globalWeekNumber);
    const directHoursBAU = personWeeklyAllocation?.totalHoursBAU || 0;
    const directHoursNonBAU = personWeeklyAllocation?.totalHoursNonBAU || 0;

    // Get team allocations for this person
    const teamAllocations: Array<{
        teamId: string;
        teamName: string;
        hoursBAU: number;
        hoursNonBAU: number;
    }> = [];

    let teamHoursBAU = 0;
    let teamHoursNonBAU = 0;

    // Check each team this person belongs to
    personResource.teams?.forEach(team => {
        const teamResource = allResources.find(r => r.id === team.id && r.type === AssignmentType.Team);
        if (teamResource) {
            const teamWeeklyAllocation = teamResource.allocations.find(a => a.globalWeekNumber === globalWeekNumber);
            if (teamWeeklyAllocation) {
                const teamBAU = teamWeeklyAllocation.totalHoursBAU || 0;
                const teamNonBAU = teamWeeklyAllocation.totalHoursNonBAU || 0;
                
                teamHoursBAU += teamBAU;
                teamHoursNonBAU += teamNonBAU;
                
                teamAllocations.push({
                    teamId: team.id,
                    teamName: team.name,
                    hoursBAU: teamBAU,
                    hoursNonBAU: teamNonBAU
                });
            }
        }
    });

    const totalHoursBAU = directHoursBAU + teamHoursBAU;
    const totalHoursNonBAU = directHoursNonBAU + teamHoursNonBAU;

    return {
        directHoursBAU,
        directHoursNonBAU,
        teamHoursBAU,
        teamHoursNonBAU,
        totalHoursBAU,
        totalHoursNonBAU,
        availableHours: personWeeklyAllocation?.availableHours,
        baseHours: personWeeklyAllocation?.baseHours,
        teamAllocations
    };
}

/**
 * Calculate total allocation for a person in the Projects view, considering team memberships
 * @param personId - The person's ID
 * @param globalWeekNumber - The week to calculate for
 * @param allProjects - All projects from ProjectsView
 * @returns PersonAllocationResult with breakdown of direct and team hours
 */
export function calculatePersonAllocationFromProjects(
    personId: string,
    globalWeekNumber: number,
    allProjects: ProjectTimeline[]
): PersonAllocationResult {
    let directHoursBAU = 0;
    let directHoursNonBAU = 0;
    let teamHoursBAU = 0;
    let teamHoursNonBAU = 0;
    let availableHours: number | undefined;
    let baseHours: number | undefined;
    
    const teamAllocations: Array<{
        teamId: string;
        teamName: string;
        hoursBAU: number;
        hoursNonBAU: number;
    }> = [];

    // Find person's teams by looking at any person resource in any project
    let personTeams: Array<{ id: string; name: string }> = [];
    
    // First pass: collect person's team memberships
    allProjects.forEach(project => {
        const personResource = project.resources.find(r => r.id === personId && r.assignmentType === AssignmentType.Person);
        if (personResource && personResource.teams) {
            personTeams = personResource.teams;
        }
    });

    // Second pass: calculate allocations
    allProjects.forEach(project => {
        // Direct person allocations
        const personResource = project.resources.find(r => r.id === personId && r.assignmentType === AssignmentType.Person);
        if (personResource) {
            personResource.allocations.forEach(allocation => {
                if (allocation.globalWeekNumber === globalWeekNumber) {
                    directHoursBAU += allocation.hoursBAU;
                    directHoursNonBAU += allocation.hoursNonBAU;
                    
                    // Extract available hours and base hours from the first matching allocation
                    if (availableHours === undefined && allocation.availableHours !== undefined) {
                        availableHours = allocation.availableHours;
                    }
                    if (baseHours === undefined && allocation.baseHours !== undefined) {
                        baseHours = allocation.baseHours;
                    }
                }
            });
        }

        // Team allocations for teams this person belongs to
        personTeams.forEach(team => {
            const teamResource = project.resources.find(r => r.id === team.id && r.assignmentType === AssignmentType.Team);
            if (teamResource) {
                teamResource.allocations.forEach(allocation => {
                    if (allocation.globalWeekNumber === globalWeekNumber) {
                        // Check if we already have this team in our allocations
                        let existingTeamAllocation = teamAllocations.find(ta => ta.teamId === team.id);
                        if (!existingTeamAllocation) {
                            existingTeamAllocation = {
                                teamId: team.id,
                                teamName: team.name,
                                hoursBAU: 0,
                                hoursNonBAU: 0
                            };
                            teamAllocations.push(existingTeamAllocation);
                        }
                        
                        existingTeamAllocation.hoursBAU += allocation.hoursBAU;
                        existingTeamAllocation.hoursNonBAU += allocation.hoursNonBAU;
                        teamHoursBAU += allocation.hoursBAU;
                        teamHoursNonBAU += allocation.hoursNonBAU;
                    }
                });
            }
        });
    });

    const totalHoursBAU = directHoursBAU + teamHoursBAU;
    const totalHoursNonBAU = directHoursNonBAU + teamHoursNonBAU;

    return {
        directHoursBAU,
        directHoursNonBAU,
        teamHoursBAU,
        teamHoursNonBAU,
        totalHoursBAU,
        totalHoursNonBAU,
        availableHours,
        baseHours,
        teamAllocations
    };
}