import {
    useMutation,
    useQueryClient,
    UseMutationResult
} from '@tanstack/react-query';
import {useCreateAuthenticatedFetch} from '../useCreateAuthenticatedFetch';
import baseUrl from '../baseUrl';
import {handleResponse} from '../handleResponse';
import ProblemDetailsError from '../ProblemDetailsError';
import {EmptyResult} from '../../types';

export interface DashboardAllocationUpdateItem {
    id: string | null; // The ID of the ProjectAllocation entity, null for new allocations
    projectId: string;
    resourceId: string; // Could be PersonId or TeamId
    globalWeekNumber: number;
    hoursBAU: number;
    hoursNonBAU: number;
}

export interface BulkUpdateDashboardAllocationsCommand {
    allocations: DashboardAllocationUpdateItem[];
    changeReason?: string;
}

export const useBulkUpdateDashboardAllocations = (): UseMutationResult<
    EmptyResult,
    Error | ProblemDetailsError,
    BulkUpdateDashboardAllocationsCommand
> => {
    const authenticatedFetch = useCreateAuthenticatedFetch();
    const queryClient = useQueryClient();

    return useMutation({
        mutationFn: async (command: BulkUpdateDashboardAllocationsCommand) => {
            const response = await authenticatedFetch(
                `${baseUrl}/api/dashboard/allocations/bulk-update`,
                {
                    method: 'POST',
                    headers: {'Content-Type': 'application/json'},
                    body: JSON.stringify(command)
                }
            );
            return handleResponse<EmptyResult>(response);
        },
        onSuccess: () => {
            // Invalidate both dashboard queries to refresh the data
            queryClient.invalidateQueries({
                queryKey: ['projects-dashboard-data']
            });
            queryClient.invalidateQueries({
                queryKey: ['resources-dashboard-data']
            });
        }
    });
};