import React, {useState, useCallback, useRef, useEffect} from 'react';

interface TooltipState {
    isVisible: boolean;
    x: number;
    y: number;
    content: React.ReactNode;
}

interface UseTooltipReturn {
    tooltipProps: {
        onMouseEnter: (event: React.MouseEvent, content: React.ReactNode) => void;
        onMouseLeave: () => void;
        onMouseMove: (event: React.MouseEvent) => void;
    };
    TooltipPortal: React.FC;
}

export const useTooltip = (): UseTooltipReturn => {
    const [tooltip, setTooltip] = useState<TooltipState>({
        isVisible: false,
        x: 0,
        y: 0,
        content: null
    });

    const timeoutRef = useRef<number | undefined>(undefined);

    const showTooltip = useCallback((event: React.MouseEvent, content: React.ReactNode) => {
        if (timeoutRef.current) {
            window.clearTimeout(timeoutRef.current);
        }

        setTooltip({
            isVisible: true,
            x: event.clientX,
            y: event.clientY - 10,
            content
        });
    }, []);

    const hideTooltip = useCallback(() => {
        timeoutRef.current = window.setTimeout(() => {
            setTooltip(prev => ({ ...prev, isVisible: false }));
        }, 100);
    }, []);

    const updateTooltipPosition = useCallback((event: React.MouseEvent) => {
        if (tooltip.isVisible) {
            setTooltip(prev => ({
                ...prev,
                x: event.clientX,
                y: event.clientY - 10
            }));
        }
    }, [tooltip.isVisible]);

    useEffect(() => {
        return () => {
            if (timeoutRef.current) {
                window.clearTimeout(timeoutRef.current);
            }
        };
    }, []);

    const TooltipPortal: React.FC = () => {
        if (!tooltip.isVisible || !tooltip.content) return null;

        return (
            <div
                style={{
                    position: 'fixed',
                    left: tooltip.x + 10,
                    top: tooltip.y,
                    zIndex: 10000,
                    pointerEvents: 'none'
                }}
            >
                {tooltip.content}
            </div>
        );
    };

    return {
        tooltipProps: {
            onMouseEnter: showTooltip,
            onMouseLeave: hideTooltip,
            onMouseMove: updateTooltipPosition
        },
        TooltipPortal
    };
};