import React from 'react';
import { EngagementContext } from '../../types';

interface EngagementContextIndicatorProps {
    engagementContext: EngagementContext;
    size?: 'sm' | 'md';
}

const EngagementContextIndicator: React.FC<EngagementContextIndicatorProps> = ({
    engagementContext,
    size = 'md'
}) => {
    const isBAU = engagementContext === EngagementContext.BAU;
    
    const sizeStyles = {
        sm: {
            fontSize: '10px',
            padding: '2px 6px',
            borderRadius: '3px'
        },
        md: {
            fontSize: '11px',
            padding: '3px 8px',
            borderRadius: '4px'
        }
    };

    const baseStyle: React.CSSProperties = {
        display: 'inline-flex',
        alignItems: 'center',
        fontWeight: '600',
        textTransform: 'uppercase' as const,
        letterSpacing: '0.5px',
        ...sizeStyles[size]
    };

    const bauStyle: React.CSSProperties = {
        ...baseStyle,
        backgroundColor: '#dbeafe',
        color: '#1e40af',
        border: '1px solid #93c5fd'
    };

    const nonBauStyle: React.CSSProperties = {
        ...baseStyle,
        backgroundColor: '#fef3c7',
        color: '#d97706',
        border: '1px solid #fbbf24'
    };

    return (
        <span
            style={isBAU ? bauStyle : nonBauStyle}
            title={isBAU 
                ? 'Business-as-usual: Routine work aligned with core responsibilities' 
                : 'Non-BAU: Work outside normal role, typically related to change or strategic initiatives'
            }
        >
            {isBAU ? 'BAU' : 'Non-BAU'}
        </span>
    );
};

export default EngagementContextIndicator;