import {useQuery, UseQueryOptions, UseQueryResult} from '@tanstack/react-query';
import baseUrl from '../baseUrl';
import {useCreateAuthenticatedFetch} from '../useCreateAuthenticatedFetch';
import {handleResponse} from '../handleResponse';
import ProblemDetailsError from '../ProblemDetailsError';
import { 
    ResourcesDashboardData
} from '../../types';

// Request parameters for the API
export interface ResourcesDashboardRequest {
    startGlobalWeekNumber?: number;
    numberOfWeeks?: number;
}

type ResourcesDashboardDataQueryOptions = Omit<
    UseQueryOptions<
        ResourcesDashboardData,
        Error | ProblemDetailsError,
        ResourcesDashboardData,
        readonly unknown[]
    >,
    'queryKey' | 'queryFn'
>;

const useGetResourcesDashboardData = (
    params: ResourcesDashboardRequest,
    options?: ResourcesDashboardDataQueryOptions
): UseQueryResult<ResourcesDashboardData, Error> => {
    const authenticatedFetch = useCreateAuthenticatedFetch();

    const queryString = params
        ? new URLSearchParams(
              Object.fromEntries(
                  Object.entries(params).filter(([, v]) => v != null)
              ) as Record<string, string>
          ).toString()
        : '';

    const url = `${baseUrl}/api/resources/dashboard-data${queryString ? `?${queryString}` : ''}`;

    const queryKey = ['resources-dashboard-data', params] as const;

    return useQuery({
        queryKey,
        queryFn: async () => {
            const response = await authenticatedFetch(url);
            return handleResponse<ResourcesDashboardData>(response);
        },
        ...options
    });
};

export default useGetResourcesDashboardData;
