import React, {useState, useMemo} from 'react';
import {
    format,
    getQuarter,
    getYear,
    addDays,
    startOfQuarter,
    addQuarters
} from 'date-fns';
import DashboardControls from './DashboardControls';
import ProjectsView from './ProjectsView';
import ResourcesView from './ResourcesView';
import {getMondayFromGlobalWeek, getWeekInfo} from '../../../lib/scheduling.ts';

// Types
export interface TimelineConfigEntry {
    units: number;
    unitName: string;
    addFn: (date: Date, amount: number) => Date;
}

export interface AppTimelineConfig {
    daily: TimelineConfigEntry;
    weekly: TimelineConfigEntry;
    monthly: TimelineConfigEntry;
}

export interface TimelineNavOption {
    label: string;
    globalWeek: number;
}

export interface TimelineColumn {
    globalWeekNumber: number;
    startDate: Date;
    endDate: Date;
    label: string;
    month: string;
    quarter: string;
    year: string;
    weekOfYear: number;
}

export interface TimelineGroup {
    label: string;
    startIndex: number;
    span: number;
}

export interface TimelineData {
    columns: TimelineColumn[];
    monthGroups: TimelineGroup[];
    quarterGroups: TimelineGroup[];
    yearGroups: TimelineGroup[];
    startGlobalWeek: number;
    endGlobalWeek: number;
}

export type DashboardViewType = 'projects' | 'resources';

export interface DashboardControlsProps {
    viewType: DashboardViewType;
    onViewTypeChange: (viewType: DashboardViewType) => void;
    onNavigate: (direction: 'prev' | 'next' | 'today') => void;
    onGoToWeek: (globalWeek: number) => void;
    navOptions: TimelineNavOption[];
}

export interface DashboardViewProps {
    timelineData: TimelineData;
}

// Configuration
const WEEKS_TO_DISPLAY = 12;

const generateTimelineNavigationOptions = (
    currentWeek: number
): TimelineNavOption[] => {
    const mondayOfCurrentWeek = getMondayFromGlobalWeek(currentWeek);
    const currentQuarterStart = startOfQuarter(mondayOfCurrentWeek);
    
    const generateQuarterOption = (quartersToAdd: number): TimelineNavOption => {
        const quarterDate = addQuarters(currentQuarterStart, quartersToAdd);
        const quarter = getQuarter(quarterDate);
        const year = getYear(quarterDate);
        
        return {
            label: `Q${quarter} ${year}`,
            globalWeek: getWeekInfo(quarterDate).globalWeekNumber
        };
    };
    
    return [
        generateQuarterOption(0), // Current Quarter
        generateQuarterOption(1), // Next Quarter
        generateQuarterOption(2), // Next 2 Quarters
        generateQuarterOption(4)  // Next 4 Quarters (1 year ahead)
    ];
};

const generateTimelineData = (
    startGlobalWeek: number,
    numberOfWeeks: number
): TimelineData => {
    const columns: TimelineColumn[] = [];

    for (let i = 0; i < numberOfWeeks; i++) {
        const currentGlobalWeek = startGlobalWeek + i;
        const weekInfo = getWeekInfo(
            getMondayFromGlobalWeek(currentGlobalWeek)
        );

        columns.push({
            globalWeekNumber: weekInfo.globalWeekNumber,
            startDate: weekInfo.mondayOfWeek,
            endDate: addDays(weekInfo.mondayOfWeek, 6), // Monday to Sunday
            label: `W${weekInfo.weekOfYear}`,
            month: format(weekInfo.mondayOfWeek, 'MMM'),
            quarter: `Q${getQuarter(weekInfo.mondayOfWeek)}`,
            year: getYear(weekInfo.mondayOfWeek).toString(),
            weekOfYear: weekInfo.weekOfYear
        });
    }

    const monthGroups = columns.reduce<TimelineGroup[]>((acc, col, index) => {
        const monthYear = `${col.month} ${col.year}`;
        let group = acc.find(g => g.label === monthYear);
        if (!group) {
            group = {label: monthYear, startIndex: index, span: 0};
            acc.push(group);
        }
        group.span++;
        return acc;
    }, []);

    const quarterGroups = columns.reduce<TimelineGroup[]>((acc, col, index) => {
        const quarterYear = `${col.quarter} ${col.year}`;
        let group = acc.find(g => g.label === quarterYear);
        if (!group) {
            group = {label: quarterYear, startIndex: index, span: 0};
            acc.push(group);
        }
        group.span++;
        return acc;
    }, []);

    const yearGroups = columns.reduce<TimelineGroup[]>((acc, col, index) => {
        const year = col.year;
        let group = acc.find(g => g.label === year);
        if (!group) {
            group = {label: year, startIndex: index, span: 0};
            acc.push(group);
        }
        group.span++;
        return acc;
    }, []);

    return {
        columns,
        monthGroups,
        quarterGroups,
        yearGroups,
        startGlobalWeek,
        endGlobalWeek: startGlobalWeek + numberOfWeeks - 1
    };
};

const DashboardContainer: React.FC = () => {
    const [viewType, setViewType] = useState<DashboardViewType>('projects');

    // STATE CHANGE: We now track the starting global week for our view
    const [startGlobalWeek, setStartGlobalWeek] = useState(
        () => getWeekInfo(new Date()).globalWeekNumber - 4
    ); // Default to show a bit of history

    // Generate timeline data based on the current state
    const timelineData = useMemo<TimelineData>(() => {
        return generateTimelineData(startGlobalWeek, WEEKS_TO_DISPLAY);
    }, [startGlobalWeek]);

    // Generate navigation options dynamically
    const navOptions = useMemo<TimelineNavOption[]>(() => {
        const currentWeek = getWeekInfo(new Date()).globalWeekNumber;
        return generateTimelineNavigationOptions(currentWeek);
    }, []);

    const navigateTimeline = (direction: 'prev' | 'next' | 'today') => {
        if (direction === 'today') {
            setStartGlobalWeek(getWeekInfo(new Date()).globalWeekNumber - 4);
            return;
        }

        const increment = direction === 'prev' ? -1 : 1;
        const step = Math.max(1, Math.floor(WEEKS_TO_DISPLAY / 2)); // Jump half the view
        setStartGlobalWeek(prevWeek => prevWeek + increment * step);
    };

    const goToWeek = (globalWeek: number) => {
        setStartGlobalWeek(globalWeek);
    };

    const controlsProps: DashboardControlsProps = {
        viewType,
        onViewTypeChange: setViewType,
        onNavigate: navigateTimeline,
        onGoToWeek: goToWeek,
        navOptions
    };

    const viewProps: DashboardViewProps = {
        timelineData
    };

    // We pass the new global week params to the hooks
    const requestParams = {
        startGlobalWeekNumber: startGlobalWeek,
        numberOfWeeks: WEEKS_TO_DISPLAY
    };

    return (
        <div
            className="dashboard-container"
            style={{
                fontFamily: 'system-ui, -apple-system, sans-serif',
                padding: '20px'
            }}
        >
            <DashboardControls {...controlsProps} />

            {viewType === 'projects' ? (
                <ProjectsView
                    timelineData={timelineData}
                    requestParams={requestParams}
                />
            ) : (
                <ResourcesView {...viewProps} requestParams={requestParams} />
            )}
        </div>
    );
};

export default DashboardContainer;
