import React from 'react';
import {DashboardControlsProps} from './DashboardContainer';

const DashboardControls: React.FC<DashboardControlsProps> = ({
    viewType,
    onViewTypeChange,
    onNavigate,
    onGoToWeek,
    navOptions
}) => {
    return (
        <div
            style={{
                marginBottom: '24px',
                padding: '20px',
                background: '#f8fafc',
                borderRadius: '8px',
                border: '1px solid #e2e8f0'
            }}
        >
            <div
                style={{
                    display: 'flex',
                    justifyContent: 'space-between',
                    alignItems: 'center',
                    flexWrap: 'wrap',
                    gap: '16px'
                }}
            >
                {/* View Type Selector and Title */}
                <div
                    style={{display: 'flex', alignItems: 'center', gap: '16px'}}
                >
                    <div
                        style={{
                            display: 'flex',
                            background: 'white',
                            borderRadius: '6px',
                            border: '1px solid #d1d5db',
                            overflow: 'hidden'
                        }}
                    >
                        <button
                            onClick={() => onViewTypeChange('projects')}
                            style={{
                                padding: '8px 16px',
                                border: 'none',
                                background:
                                    viewType === 'projects'
                                        ? '#4F46E5'
                                        : 'white',
                                color:
                                    viewType === 'projects'
                                        ? 'white'
                                        : '#374151',
                                cursor: 'pointer',
                                fontSize: '14px',
                                fontWeight: '500',
                                borderRight: '1px solid #d1d5db'
                            }}
                        >
                            Projects
                        </button>
                        <button
                            onClick={() => onViewTypeChange('resources')}
                            style={{
                                padding: '8px 16px',
                                border: 'none',
                                background:
                                    viewType === 'resources'
                                        ? '#4F46E5'
                                        : 'white',
                                color:
                                    viewType === 'resources'
                                        ? 'white'
                                        : '#374151',
                                cursor: 'pointer',
                                fontSize: '14px',
                                fontWeight: '500'
                            }}
                        >
                            Resources
                        </button>
                    </div>

                    <h1
                        style={{
                            margin: 0,
                            color: '#1e293b',
                            fontSize: '24px',
                            fontWeight: '600'
                        }}
                    >
                        {viewType === 'projects' ? 'Projects' : 'Resources'}
                    </h1>
                </div>

                {/* Controls */}
                <div
                    style={{
                        display: 'flex',
                        gap: '12px',
                        alignItems: 'center',
                        flexWrap: 'wrap'
                    }}
                >
                    {/* Jump To Buttons */}
                    <button
                        onClick={() => onNavigate('today')}
                        style={{
                            padding: '8px 12px',
                            border: '1px solid #d1d5db',
                            borderRadius: '4px',
                            background: 'white',
                            cursor: 'pointer',
                            fontSize: '14px'
                        }}
                    >
                        Today
                    </button>
                    {navOptions.map(opt => (
                        <button
                            key={opt.globalWeek}
                            onClick={() => onGoToWeek(opt.globalWeek)}
                            style={{
                                padding: '8px 12px',
                                border: '1px solid #d1d5db',
                                borderRadius: '4px',
                                background: 'white',
                                cursor: 'pointer',
                                fontSize: '14px'
                            }}
                        >
                            {opt.label}
                        </button>
                    ))}

                    {/* Navigation Buttons */}
                    <button
                        onClick={() => onNavigate('prev')}
                        style={{
                            padding: '8px 12px',
                            border: '1px solid #d1d5db',
                            borderRadius: '4px',
                            background: 'white',
                            cursor: 'pointer',
                            fontSize: '14px'
                        }}
                    >
                        {'<'}
                    </button>
                    <button
                        onClick={() => onNavigate('next')}
                        style={{
                            padding: '8px 12px',
                            border: '1px solid #d1d5db',
                            borderRadius: '4px',
                            background: 'white',
                            cursor: 'pointer',
                            fontSize: '14px'
                        }}
                    >
                        {'>'}
                    </button>
                </div>
            </div>
        </div>
    );
};

export default DashboardControls;
