import { AssignmentType } from '../types';
import { BaseWorkingHours, CompanyNonWorkingDay, PersonalLeave } from '../api/working-hours/models';

/**
 * Client-side working hours calculator for computing available hours
 * when we don't have backend-calculated data available (e.g., for new allocations)
 */

export interface ClientWorkingHoursData {
    baseWorkingHours: BaseWorkingHours | null;
    companyNonWorkingDays: CompanyNonWorkingDay[];
    personalLeave: PersonalLeave[];
}

/**
 * Calculate available hours for a specific resource and week on the client side
 * This is used when creating new allocations before they're saved to the backend
 */
export function calculateClientSideAvailableHours(
    resourceId: string,
    resourceType: AssignmentType,
    globalWeekNumber: number,
    workingHoursData: ClientWorkingHoursData
): number {
    const { baseWorkingHours, companyNonWorkingDays, personalLeave } = workingHoursData;
    
    // Start with base hours (default to 40 if not configured)
    const baseHours = baseWorkingHours?.baseHoursPerWeek ?? 40;
    
    // Calculate company non-working hours for this week
    const companyNonWorkingHours = calculateCompanyNonWorkingHoursForWeek(
        globalWeekNumber,
        companyNonWorkingDays,
        baseHours
    );
    
    // For persons, also subtract personal leave hours
    let personalLeaveHours = 0;
    if (resourceType === AssignmentType.Person) {
        personalLeaveHours = calculatePersonalLeaveHoursForWeek(
            resourceId,
            globalWeekNumber,
            personalLeave,
            baseHours
        );
    }
    
    // Available Hours = Base Hours - Company Non-Working Hours - Personal Leave Hours
    const availableHours = Math.max(0, baseHours - companyNonWorkingHours - personalLeaveHours);
    
    return availableHours;
}

/**
 * Calculate company non-working hours for a specific week
 */
function calculateCompanyNonWorkingHoursForWeek(
    _globalWeekNumber: number,
    _companyNonWorkingDays: CompanyNonWorkingDay[],
    _baseHoursPerWeek: number
): number {
    // This is a simplified calculation - in reality we'd need to convert
    // globalWeekNumber to actual dates and check overlaps with company non-working periods
    // For now, we'll return 0 and let the backend handle the accurate calculation
    // TODO: Implement proper date range overlap calculation if needed
    return 0;
}

/**
 * Calculate personal leave hours for a specific person and week
 */
function calculatePersonalLeaveHoursForWeek(
    _personId: string,
    _globalWeekNumber: number,
    _personalLeave: PersonalLeave[],
    _baseHoursPerWeek: number
): number {
    // This is a simplified calculation - in reality we'd need to convert
    // globalWeekNumber to actual dates and check overlaps with personal leave periods
    // For now, we'll return 0 and let the backend handle the accurate calculation
    // TODO: Implement proper date range overlap calculation if needed
    return 0;
}

/**
 * Get available hours from existing allocation data if available,
 * otherwise calculate using client-side logic
 */
export function getAvailableHoursForResourceWeek(
    resourceId: string,
    resourceType: AssignmentType,
    globalWeekNumber: number,
    existingAllocations: any[], // Existing allocations that might have availableHours
    workingHoursData: ClientWorkingHoursData
): number {
    // First, try to find existing allocation data with availableHours
    const existingAllocation = existingAllocations.find(
        alloc => alloc.globalWeekNumber === globalWeekNumber && alloc.availableHours !== undefined
    );
    
    if (existingAllocation?.availableHours !== undefined) {
        return existingAllocation.availableHours;
    }
    
    // Fall back to client-side calculation
    return calculateClientSideAvailableHours(resourceId, resourceType, globalWeekNumber, workingHoursData);
}